@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

:root {
    --color-bg: #f6f6fa;
    --color-bg-variant: #0b0b3f;
    --color-primary: #301f6e;
    --color-primary-variant: rgba(39, 42, 44, 0.4);
    --color-white: rgb(33, 11, 71);
    --color-light: rgba(20, 6, 36, 0.6);
    --transition: all 400ms ease;
    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-ms: 90%;
    --container-width-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=1)";
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
    font-family: 'Poppins', sans-serif;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    /* background-image: url(../src/assets/map.png); */
}


/* ===================General Styles ==================*/

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
    padding: 0 1em;
    max-width: 60rem;
    place-items: center;
    justify-content: center;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 10rem;
    height: 150vh;
}

section>h2,
section>h5 {
    text-align: center;
    color: var(--color-light);
}

section>h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-white);
}

.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
}


/*==================GRAPHY CHART =====================*/

.chartBox {
    width: 70%;
    height: 40%;
    padding: 0 1em;
    max-width: 60rem;
    margin: 0 auto;
    border: 2px;
}


/* ===================== MEDIA QUERIES (MEDIUM DEVICES ================*/

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }
    section {
        margin-top: 6rem;
    }
}


/* ===================== MEDIA QUERIES (SMALL DEVICES ================*/

@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }
    section>h2 {
        margin-bottom: 2rem;
    }
}